import React, { Component } from 'react'
import { BrowserRouter, Match, Miss, Link } from 'react-router'
import { Provider, observer } from 'mobx-react'

import SmallButton from 'components/CoreUI/Buttons/SmallButton.jsx'

import './styles/Header.styl'

const Header = ({ siteName, currentHealth }) => (
	<div class="_header">
		<div class="left">
			<h3>{siteName}</h3>
			<h5>{currentHealth}% <span>Health</span></h5>
		</div>

		<div class="right">
			<SmallButton color="pink" text="REPORTS"/>
			<SmallButton color="yellow" text="STATISTICS"/>
		</div>
	</div>
)

export default Header


