export function reduceOpacityOf(hex) {
	return {
		to(dec) {
			if (!/^#?([A-Fa-f0-9]{3}){1,2}$/.test(hex)) {
				return new Error('Invalid hexidecimal.')
			}
			
			const noHashHex = (hex[0] === '#')
				? hex.substr(1)
				: hex
			
			const newHex = `0x${noHashHex}`
			return `rgba(${[
				(newHex>>16)&255,
				(newHex>>8)&255,
				newHex&255
			].join(', ')}, ${dec})`
		}
	}
}


