import React, { Component } from 'react'
import { BrowserRouter, Match, Miss, Link } from 'react-router'
import { Provider, observer } from 'mobx-react'
import { AreaChart, Area, linearGradient, Tooltip, CartesianGrid, XAxis, YAxis, ResponsiveContainer } from 'recharts'

import VarStore from 'stores/VarStore'
import { reduceOpacityOf } from 'functions/colorFns'

import './styles/Graph.styl'

const Graph = ({ data }) => {
	const
		{ colors } = VarStore,
		transparentPink = reduceOpacityOf(colors.Pink).to(0.375),
		kindaTransparentPink = reduceOpacityOf(colors.Pink).to(0.75),
		transparentYellow = reduceOpacityOf(colors.Yellow).to(0.45),
		kindaTransparentYellow = reduceOpacityOf(colors.Yellow).to(0.75)

	const toolTipStyle = {
		color: colors.MediumGray,
		fontSize: '1.4rem',
		fontFamily: '"Montserrat", sans-serif'
	}

	return (
		<div class="_graph">
			<ResponsiveContainer minWidth="102%" width="102%" height={150}>
				<AreaChart data={data}>
					<defs>
						<filter id="pink" x1="0" y1="0" x2="0" y2="1">
      				<feGaussianBlur result="blurOut" in="offOut" stdDeviation="12" />
     				<feBlend in="SourceGraphic" in2="blurOut" mode="normal" />
						</filter>
						<linearGradient id="yellow" x1="0" y1="0" x2="0" y2="1">
							<stop offset="5%" stopColor={transparentYellow} />
							<stop offset="95%" stopColor={colors.Yellow} />
						</linearGradient>
						<filter id="pink" x1="0" y1="0" x2="0" y2="1">
      				<feGaussianBlur result="blurOut" in="offOut" stdDeviation="8" />
     					<feBlend in="SourceGraphic" in2="blurOut" mode="normal" />
						</filter>
						<filter id="yellow" x1="0" y1="0" x2="0" y2="1">
      				<feGaussianBlur result="blurOut" in="offOut" stdDeviation="8" />
     					<feBlend in="SourceGraphic" in2="blurOut" mode="normal" />
						</filter>
					</defs>
					<YAxis hide={true} tick={{stroke: 'red', strokeWidth: 1}} />
					<Tooltip itemStyle={toolTipStyle}/>
					<Area type="natural" dataKey="b" strokeWidth={2} stroke={kindaTransparentYellow}
						fillOpacity={1} fill={transparentYellow} filter="url(#yellow)" />
					<Area type="natural" dataKey="a" strokeWidth={2} stroke={kindaTransparentPink}
						fillOpacity={1} fill={transparentPink} filter="url(#pink)" />
				</AreaChart>
			</ResponsiveContainer>
		</div>
	)
}

export default Graph


