import React, { Component } from 'react'
import { BrowserRouter, Match, Miss, Link } from 'react-router'
import { Provider, observer } from 'mobx-react'

import Header from './Header.jsx'
import Footer from './Footer.jsx'
import Graph from './Graph.jsx'

import './styles/SiteGraph.styl'

const SiteGraph = ({ siteName, currentHealth, data }) => {
	const stats = [
		{statName: 'TRAFFIC', statLiteral: '128K'},
		{statName: 'TRANSFER', statLiteral: '12GB/s'},
		{statName: 'BALLOONS', statLiteral: '99 RED'},
		{statName: 'TEMP (°F)', statLiteral: '212°'},
	]

	return (
		<div class="_site-graph">
			<Header siteName={siteName} currentHealth={currentHealth} />
			<Graph data={data} />
			<Footer stats={stats}/>
		</div>
	)
}

export default SiteGraph


